.base-footer {
  margin: 0 auto;
  padding: 40px 0;
  width: 100%;
  max-width: min(1200px, 90vw);
  display: flex;
  justify-content: space-between;
  .footer-l {
    display: flex;
    flex-wrap: wrap;
    .footer-list {
      flex: 1;
      margin-right: 70px;
      text-align: left;
      &:last-child {
        margin-right: 0;
      }
      .footer-title {
        white-space: nowrap;
        margin-bottom: 16px;
        font-size: 14px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #fff;
        line-height: 18px;
      }
      ul {
        li {
          margin-bottom: 16px;
          &:last-child {
            margin-bottom: 0;
          }
        }
        a {
          font-size: 14px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          line-height: 20px;
          white-space: nowrap;
          color: rgba(255, 255, 255, 0.5);
          transition: color 0.1s;
          &:hover {
            color: #fff;
          }
        }
      }
    }
  }
  .footer-r {
    flex: 1;
    .sun-logo {
      display: flex;
      justify-content: flex-end;
      img {
        display: block;
        width: 152px;
      }
    }
    .copyright {
      margin-top: 20px;
      font-size: 12px;
      font-family: Helvetica;
      color: rgba(255, 255, 255, 0.5);
      line-height: 14px;
      text-align: right;
    }
  }

  .menu-hover-dropdown {
    position: relative;
    cursor: pointer;
    font-size: 0;

    .lang-link {
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #ffffff;
      white-space: nowrap;
    }

    .down-icon {
      margin-left: 6px;
      height: 10px;
      width: 10px;
      background-image: url(../images/down-white.svg);
      background-repeat: no-repeat;
      transition: transform 0.2s;
    }

    &:hover .down-icon {
      transform: rotate(-180deg);
    }

    &:hover .header-select-box {
      display: block;
    }

    .header-select-box,
    .header-select-box-pool {
      display: none;
      font-size: 14px;
      position: absolute;
      left: 0;
      border-radius: 10px;
      z-index: 3;

      .lang {
        width: 86px;
        padding: 8px 0;
        margin: 0;
        text-align: center;
        border-radius: 10px;
      }

      .header-wrapper {
        background: #342b43;
        border-radius: 10px;
      }

      .h15 {
        height: 8px;
      }

      li.selected,
      li:hover {
        background-color: #3e354c;
      }

      li a {
        display: inline-block;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #ffffff;
        line-height: 20px;
      }
    }
  }
  .foot-hide-for-pc {
    display: block;
  }
  .foot-hide-for-mobile {
    display: none;
  }
}

@media screen and (max-width: 992px) {
  .base-footer {
    .footer-l .footer-list {
      margin-right: 0;
      flex: auto;
      &:nth-child(2n + 1) {
        width: 60%;
      }
      &:nth-child(2n) {
        width: 40%;
      }
      &:nth-child(3),
      &:nth-child(4) {
        margin-top: 30px;
      }
    }
    .footer-r {
      margin-top: 30px;
      .sun-logo {
        justify-content: flex-start;
        img {
          width: auto;
          height: 30px;
        }
      }
    }
    .foot-hide-for-pc {
      display: none;
    }
    .foot-hide-for-mobile {
      display: block;
    }
  }
}
