    padding: 24px 20px 20px 20px;
    text-align: center;
  }
  img {
    width: 94px;
    height: 95px;
  }
  p {
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333333;
    line-height: 25px;
    margin: 22px 0 16px;
    text-align: left;
  }
